import React from 'react';
import cls from './style.module.scss';
import ProfileStatus from './ProfileStatus';
import EditFullNameForm from './ProfileFullName';
import EditLookForJobForm from './LookForJob';
import EditLookForJobDescForm from './LookForJobDesc';
import EditContactForm from './Contact';
import EditAboutMeForm from './AboutMe';
import { updateContactThunkCreator } from '../../../reducer/ProfilePageReducer';
function ProfileInfo(props) {
	return (
		<div className={cls.profile__info}>
			<label htmlFor="avatar" onClick={e => !props.isOwner && e.preventDefault()}>
				<div
					className={cls.avatar}
					style={{
						backgroundImage: `url(${
							props.profileFoto !== null
								? props.profileFoto.large
								: 'data:image/png;base64,'
						})`,
					}}
				/>
			</label>
			<input
				type="file"
				id="avatar"
				onChange={e => {
					props.updatePhotoThunkCreator(e.target.files.length && e.target.files[0]);
				}}
			/>

			<div className={cls.info}>
				<EditFullNameForm {...props} updateFullName={props.updateFullNameThunkCreator} />
				<ProfileStatus status={props.status} updateStatus={props.updateUserStatusThunkCreator} />
				<EditAboutMeForm {...props} updateAboutMe={props.updateAboutMeThunkCreator} />
				<EditLookForJobForm {...props} updateLookForJob={props.updateLookForJobThunkCreator} />
				<EditLookForJobDescForm {...props} updateLookForJobDesc={props.updateLookForJobDescThunkCreator} />
				<EditContactForm {...props} updateContact={props.updateContactThunkCreator} />
			</div>
		</div>
	);
}

export default ProfileInfo;
