import React from 'react';
import cls from './style.module.css';
const Friend = (props) => {
    return (

        <div className={cls.friend}>
            <div className={cls.img} style={{ backgroundImage: 'url(data:image/png;base64,)'}}/>
            <p className={cls.name}>{props.name}</p>
        </div>
    )
}

export default Friend;