import { ProfileApi } from '../../api/api';

const TOGGLE_FETCHING = 'TOGGLE_FETCHING';
const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_USER_STATUS = 'SET_USER_STATUS';
const DEL_POST = 'DEL_POST';
const SET_FULL_NAME = 'SET_FULL_NAME';
const SET_LOOK_JOB = 'SET_LOOK_JOB';
const SET_LOOK_JOB_DESC = 'SET_LOOK_JOB_DESC';
const SET_ABOUT_ME = 'SET_ABOUT_ME';
const SET_PHOTO = 'SET_PHOTO';
const SET_CONTACT = 'SET_CONTACT';

let InitialState = {
	PostData: [
		{ id: 1, text: 'asdas', like: 10 },
		{ id: 2, text: 'asdasd', like: 10 },
		{ id: 3, text: 'asdasd', like: 10 },
		{ id: 4, text: 'Galasdia', like: 10 },
	],
	profileFoto: null,
	isFetching: false,
	status: '',
	fullName: '',
	aboutMe: '',
	jobDesc: '',
	jobSearch: false,
	contacts: {
		facebook: 'empty',
		github: 'empty',
		instagram: 'empty',
		mainLink: 'empty',
		twitter: 'empty',
		vk: 'empty',
		website: 'empty',
		youtube: 'empty',
	},
};

let ProfilePageReducer = (state = InitialState, action) => {
	switch (action.type) {
		case ADD_POST:
			return {
				...state,
				textArea: '',
				PostData: [...state.PostData, { id: 32, like: 10, text: action.NewPost }],
			};
		case DEL_POST:
			return {
				...state,
				PostData: state.PostData.filter(e => e.id !== action.id),
			};
		case TOGGLE_FETCHING:
			return {
				...state,
				isFetching: action.isFetching,
			};
		case SET_USER_PROFILE:
			return {
				...state,
				profileFoto: action.profile.photos,
				fullName: action.profile.fullName,
				aboutMe: action.profile.aboutMe,
				jobDesc: action.profile.lookingForAJobDescription,
				jobSearch: action.profile.lookingForAJob,
				contacts: action.profile.contacts,
			};
		case SET_USER_STATUS:
			return {
				...state,
				status: action.status,
			};
		case SET_FULL_NAME:
			return {
				...state,
				fullName: action.fullName,
			};
		case SET_LOOK_JOB:
			return {
				...state,
				jobSearch: action.lookingForAJob,
			};
		case SET_LOOK_JOB_DESC:
			return {
				...state,
				jobDesc: action.lookingForAJobDesc,
			};
		case SET_ABOUT_ME:
			return {
				...state,
				aboutMe: action.aboutMe,
			};
		case SET_PHOTO:
			return {
				...state,
				profileFoto: action.photo,
			};
		case SET_CONTACT:
			debugger;
			return {
				...state,
				contacts: { ...state.contacts, [action.contactName]: action.contactVal },
			};

		default:
			return state;
	}
};
export default ProfilePageReducer;

export const AddPostActionCreator = NewPost => ({ type: ADD_POST, NewPost });
export const DeletePostActionCreator = id => ({ type: DEL_POST, id });
export const SetUserProfile = profile => ({ type: SET_USER_PROFILE, profile });
export const setUserStatus = status => ({ type: SET_USER_STATUS, status });

export const updateFullName = fullName => ({ type: SET_FULL_NAME, fullName });
export const updateLookForJob = lookingForAJob => ({ type: SET_LOOK_JOB, lookingForAJob });
export const updateLookForJobDesc = lookingForAJobDesc => ({ type: SET_LOOK_JOB_DESC, lookingForAJobDesc });
export const updateAboutMe = aboutMe => ({ type: SET_ABOUT_ME, aboutMe });
export const updatePhoto = photo => ({ type: SET_PHOTO, photo });
export const updateContact = (contactName, contactVal) => ({ type: SET_CONTACT, contactName, contactVal });

export const SetIsFetching = isFetching => ({ type: TOGGLE_FETCHING, isFetching });

export const getProgileInfoThunkCreator = userId => async dispatch => {
	dispatch(SetIsFetching(true));
	let response = await ProfileApi.getProgileInfo(userId);
	dispatch(SetUserProfile(response));
	dispatch(SetIsFetching(false));
};

export const getUserStatusThunkCreator = userId => async dispatch => {
	let response = await ProfileApi.getStatus(userId);
	if (response.status === 200) {
		dispatch(setUserStatus(response.data));
	}
};

export const updateFullNameThunkCreator = (fullName, oldAboutMe, oldDesc) => async dispatch => {
	let response = await ProfileApi.setFullName(fullName, oldAboutMe, oldDesc);
	if (response.resultCode === 0) {
		dispatch(updateFullName(fullName));
	}
};

export const updateAboutMeThunkCreator = (aboutMe, oldDesc, oldFullName) => async dispatch => {
	let response = await ProfileApi.setAboutMe(aboutMe, oldDesc, oldFullName);
	if (response.resultCode === 0) {
		dispatch(updateAboutMe(aboutMe));
	}
};

export const updateContactThunkCreator = (
	contacts,
	contactName,
	contactVal,
	oldAboutMe,
	oldLookingForAJobDesc,
	oldFullName,
) => async dispatch => {
	let response = await ProfileApi.setContact(
		contacts,
		contactName,
		contactVal,
		oldAboutMe,
		oldLookingForAJobDesc,
		oldFullName,
	);
	if (response.resultCode === 0) {
		dispatch(updateContact(contactName, contactVal));
	}
};

export const updateLookForJobThunkCreator = (lookingForAJob, oldFullName, oldAboutMe, oldDesc) => async dispatch => {
	let response = await ProfileApi.setLookForJob(lookingForAJob, oldFullName, oldAboutMe, oldDesc);
	if (response.resultCode === 0) {
		dispatch(updateLookForJob(lookingForAJob));
	}
};

export const updatePhotoThunkCreator = photo => async dispatch => {
	let response = await ProfileApi.setPhoto(photo);
	if (response.resultCode === 0) {
		dispatch(updatePhoto(response.data.photos));
	}
};
export const updateLookForJobDescThunkCreator = (
	lookingForAJobDesc,
	oldFullName,
	oldAboutMe,
	oldDesc,
) => async dispatch => {
	let response = await ProfileApi.setLookForJobDesc(lookingForAJobDesc, oldFullName, oldAboutMe, oldDesc);
	if (response.resultCode === 0) {
		dispatch(updateLookForJobDesc(lookingForAJobDesc));
	}
};

export const updateUserStatusThunkCreator = status => async dispatch => {
	let response = await ProfileApi.updateStatus(status);
	if (response.resultCode === 0) {
		dispatch(setUserStatus(status));
	}
};
