import { AuthApi, SecurityApi } from '../../api/api';
import { stopSubmit } from 'redux-form';

const SET_USER_DATA = 'SET_USER_DATA';
const TOGGLE_PENNDING = 'TOGGLE_PENNDING';
const SET_CAPTCHA_URL = 'SET_CAPTCHA_URL';

let InitialState = {
	id: null,
	email: null,
	login: null,
	isAuth: false,
	isPending: false,
	captchaUrl: '',
};

let AuthReducer = (state = InitialState, action) => {
	switch (action.type) {
		case SET_USER_DATA:
			return {
				...state,
				...action.data,
			};
		case TOGGLE_PENNDING:
			return {
				...state,
				isPending: action.turn,
			};
		case SET_CAPTCHA_URL:
			return {
				...state,
				captchaUrl: action.url,
			};
		default:
			return state;
	}
};

export const setPending = turn => ({
	type: TOGGLE_PENNDING,
	turn,
});
export const setCaptchUrl = url => ({
	type: SET_CAPTCHA_URL,
	url,
});

export const setUserData = (id, email, login, isAuth) => ({
	type: SET_USER_DATA,
	data: {
		id,
		email,
		login,
		isAuth,
	},
});

export const getMeThunkCreator = () => async dispatch => {
	let response = await AuthApi.me();
	if (response.resultCode === 0) {
		let { id, email, login } = response.data;
		dispatch(setUserData(id, email, login, true));
		dispatch(setPending(false));
	}
};

export const loginThunkCreator = (email, password, rememberMe, captcha) => async dispatch => {
	let response = await AuthApi.login(email, password, rememberMe, captcha).catch(() => {
		dispatch(setPending(false));
	});

	if (response.resultCode === 0) {
		dispatch(getMeThunkCreator());
	} else {
		if (response.resultCode === 10) {
			dispatch(getCaptchUrlThunkCreator());
		}
		let message = response.messages.length > 0 ? response.messages[0] : 'Some error';
		dispatch(stopSubmit('loginFrom', { _error: message }));
		dispatch(setPending(false));
	}
};
export const logoutThunkCreator = () => async dispatch => {
	let response = await AuthApi.logout();
	if (response.resultCode === 0) {
		dispatch(setUserData(null, null, null, false));
	}
};
export const getCaptchUrlThunkCreator = () => async dispatch => {
	let response = await SecurityApi.getCaptchUrl();

	dispatch(setCaptchUrl(response.data.url));
};

export default AuthReducer;
