import React, { useEffect, useState } from 'react';
import cls from './style.module.scss';

let ContactItem = props => {
	let [editMode, SetEditMode] = useState(false);
	let [item, SetItem] = useState(props.item);

	return (
		<div className={`line ${cls.line}`}>
			<p>{props.name}:</p>
			<p
				onDoubleClick={() => {
					props.isOwner && SetEditMode(true);
				}}
			>
				{!editMode && (props.item === null ? `empty` : props.item)}

				{editMode && (
					<input
						onChange={e => {
							SetItem(e.currentTarget.value);
						}}
						autoFocus
						onBlur={() => {
							SetEditMode(false);
							props.updateContact(
								props.serchPageInfo.contacts,
								props.name,
								item,
								props.serchPageInfo.aboutMe,
								props.serchPageInfo.jobDesc,
								props.serchPageInfo.fullName,
							);
						}}
						value={item}
					/>
				)}
			</p>
		</div>
	);
};

export default ContactItem;
