import React, { useEffect, useState } from 'react';
import cls from './style.module.scss';

let EditLookForJobForm = props => {
	let [editMode, SetEditMode] = useState(false);
	let [lookingForAJob, SetLookingForAJob] = useState(props.serchPageInfo.jobSearch);

	useEffect(
		() => {
			SetLookingForAJob(props.serchPageInfo.jobSearch);
		},
		[props.serchPageInfo.jobSearch],
	);

	return (
		<div className={`line ${cls.line}`}>
			<p>Looking for a job : </p>
			<p
				onDoubleClick={() => {
					props.isOwner && SetEditMode(true);
				}}
			>
				{!editMode ? (props.serchPageInfo.jobSearch ? `yes` : `no`) : null}
				{editMode && (
					<select
						onChange={e => {
							SetLookingForAJob(e.currentTarget.value === 'yes' ? true : false);
						}}
						autoFocus
						onBlur={() => {
							SetEditMode(false);
							props.updateLookForJob(
								lookingForAJob,
								props.serchPageInfo.fullName,
								props.serchPageInfo.aboutMe,
								props.serchPageInfo.jobDesc,
							);
						}}
					>
						<option selected={props.serchPageInfo.jobSearch ? true : false}>yes</option>
						<option selected={props.serchPageInfo.jobSearch ? false : true}>no</option>
					</select>
				)}
			</p>
		</div>
	);
};

export default EditLookForJobForm;
