import React from 'react';
import Profile from './index';
import { connect } from 'react-redux';
import {
	getProgileInfoThunkCreator,
	getUserStatusThunkCreator,
	updateAboutMeThunkCreator,
	updateContactThunkCreator,
	updateFullNameThunkCreator,
	updateLookForJobDescThunkCreator,
	updateLookForJobThunkCreator,
	updatePhotoThunkCreator,
	updateUserStatusThunkCreator,
} from '../../reducer/ProfilePageReducer';
import { withRouter } from 'react-router-dom';
import { withAuthRedirect } from '../../hoc/withAuth';
import { compose } from 'redux';
import { getProfilePage } from '../../userSelector';

class ContainerProfile extends React.Component {
	componentDidMount() {
		let userId = this.props.match.params.userId;
		if (!userId) {
			userId = this.props.myId;
		}
		this.props.getProgileInfoThunkCreator(userId);
		this.props.getUserStatusThunkCreator(userId);
	}

	componentDidUpdate(prevProps) {
		if (this.props.match.params.userId !== prevProps.match.params.userId) {
			let userId = this.props.match.params.userId;
			if (!userId) {
				userId = this.props.myId;
			}
			this.props.getProgileInfoThunkCreator(userId);
			this.props.getUserStatusThunkCreator(userId);
		}
	}

	render() {
		return <Profile {...this.props} isOwner={!!!this.props.match.params.userId} />;
	}
}

let mapStateToProps = state => {
	return {
		serchPageInfo: getProfilePage(state),
		profileFoto: getProfilePage(state).profileFoto,
		isFetching: getProfilePage(state).isFetching,
		status: getProfilePage(state).status,
		myId: state.AuthPage.id,
	};
};

export default compose(
	connect(
		mapStateToProps,
		{
			getProgileInfoThunkCreator,
			getUserStatusThunkCreator,
			updateFullNameThunkCreator,
			updateLookForJobThunkCreator,
			updateLookForJobDescThunkCreator,
			updateUserStatusThunkCreator,
			updateAboutMeThunkCreator,
			updatePhotoThunkCreator,
			updateContactThunkCreator,
		},
	),
	withAuthRedirect,
	withRouter,
)(ContainerProfile);
