import React, { useEffect, useState } from 'react';
import cls from './style.module.scss';

let EditAboutMeForm = props => {
	let [editMode, SetEditMode] = useState(false);
	let [aboutMe, SetAboutMe] = useState(props.serchPageInfo.aboutMe);

	useEffect(
		() => {
			SetAboutMe(props.serchPageInfo.aboutMe);
		},
		[props.serchPageInfo.aboutMe],
	);

	return (
		<div className={`line ${cls.line}`}>
			<p>AboutMe : </p>
			<p
				onDoubleClick={() => {
					props.isOwner && SetEditMode(true);
				}}
			>
				{!editMode && props.serchPageInfo.aboutMe}
				{editMode && (
					<textarea
						value={aboutMe}
						onChange={e => {
							SetAboutMe(e.currentTarget.value);
						}}
						autoFocus
						onBlur={() => {
							SetEditMode(false);
							props.updateAboutMe(aboutMe, props.serchPageInfo.jobDesc, props.serchPageInfo.fullName);
						}}
					/>
				)}
			</p>
		</div>
	);
};

export default EditAboutMeForm;
