import React from 'react';
import cls from './style.module.css';

function Post(props) {
	const delPost = () => {
		// props.delPost(2);
	};
	return (
		<div onClick={delPost} className={`line ${cls.post}`}>
			<div
				className={cls.img}
				style={{
					backgroundImage:
						'url(data:image/png;base64,)',
				}}
			/>
			<p className={cls.text}>{props.text}</p>
			<span className={cls.like}>{props.like}</span>
		</div>
	);
}

export default Post;
