import * as axios from 'axios/index';

const instance = axios.create({
	withCredentials: true,
	baseURL: 'https://social-network.samuraijs.com/api/1.0',
	headers: {
		'API-KEY': '8ebe5dbb-b80a-4d2a-bd2a-c3c6ac269af8',
	},
});

export const UserApi = {
	getUsers(currentPage = 1, pageSize = 10) {
		return instance.get(`/users?page=${currentPage}&count=${pageSize}`);
	},
};
export const SecurityApi = {
	getCaptchUrl() {
		return instance.get(`security/get-captcha-url`);
	},
};

export const FollowApi = {
	unfollow(id = 1) {
		return instance.delete(`/follow/${id}`).then(response => response.data);
	},

	follow(id = 1) {
		return instance.post(`/follow/${id}`, {}).then(response => response.data);
	},
};

export const AuthApi = {
	me() {
		return instance.get(`/auth/me`).then(response => response.data);
	},
	login(email, password, rememberMe = false, captcha = null) {
		return instance.post(`/auth/login`, { email, password, rememberMe, captcha }).then(response => response.data);
	},

	logout() {
		return instance.delete(`/auth/login`).then(response => response.data);
	},
};

export const ProfileApi = {
	getProgileInfo(userId) {
		return instance.get(`/profile/${userId}`).then(response => response.data);
	},
	getStatus(userId) {
		return instance.get(`/profile/status/${userId}`);
	},
	updateStatus(status) {
		return instance.put(`/profile/status`, { status: status }).then(response => response.data);
	},
	setFullName(fullName, oldAboutMe, oldDesc) {
		return instance
			.put(`/profile`, {
				fullName: fullName,
				aboutMe: oldAboutMe,
				lookingForAJobDescription: oldDesc,
			})
			.then(response => response.data);
	},

	setLookForJob(lookingForAJob, oldFullName, oldAboutMe, oldDesc) {
		return instance
			.put(`/profile`, {
				fullName: oldFullName,
				aboutMe: oldAboutMe,
				lookingForAJobDescription: oldDesc,
				lookingForAJob: lookingForAJob,
			})
			.then(response => response.data);
	},
	setLookForJobDesc(lookingForAJobDesc, oldFullName, oldAboutMe, oldLookingForAJob) {
		return instance
			.put(`/profile`, {
				fullName: oldFullName,
				aboutMe: oldAboutMe,
				lookingForAJobDescription: lookingForAJobDesc,
				lookingForAJob: oldLookingForAJob,
			})
			.then(response => response.data);
	},

	setPhoto(photo) {
		let formData = new FormData();
		formData.append('image', photo);
		return instance
			.put(`/profile/photo`, formData, {
				headers: {
					'Content-Type': 'multipart/form-data',
				},
			})
			.then(response => response.data);
	},

	setAboutMe(aboutMe, oldLookingForAJobDesc, oldFullName) {
		return instance
			.put(`/profile`, {
				fullName: oldFullName,
				aboutMe: aboutMe,
				lookingForAJobDescription: oldLookingForAJobDesc,
			})
			.then(response => response.data);
	},
	setContact(contacts, contactName, contactVal, oldAboutMe, oldLookingForAJobDesc, oldFullName) {
		return instance
			.put(`/profile`, {
				fullName: oldFullName,
				aboutMe: oldAboutMe,
				lookingForAJobDescription: oldLookingForAJobDesc,
				contacts: { ...contacts, [contactName]: contactVal },
			})
			.then(response => response.data);
	},
};
