import React from 'react';
import cls from './style.module.scss';
import ContactItem from './ContactItem';

let EditContactForm = props => {
	console.log(Object.keys(props.serchPageInfo.contacts));
	return (
		<>
			<div className={`line ${cls.line}`}>
				<div className={`line ${cls.line}`}>
					<p>Contact</p>
				</div>
			</div>
			{Object.keys(props.serchPageInfo.contacts).map(key => (
				<ContactItem {...props} item={props.serchPageInfo.contacts[key]} name={key} />
			))}
		</>
	);
};

export default EditContactForm;
