import React, { useEffect, useState } from 'react';
import cls from './style.module.scss';

let EditFullNameForm = props => {
	let [editMode, SetEditMode] = useState(false);
	let [fullName, SetFullName] = useState(props.serchPageInfo.fullName);

	useEffect(
		() => {
			SetFullName(props.serchPageInfo.fullName);
		},
		[props.serchPageInfo.fullName],
	);

	return (
		<div>
			<p
				className={cls.name}
				onDoubleClick={() => {
					props.isOwner && SetEditMode(true);
				}}
			>
				{!editMode && props.serchPageInfo.fullName}
				{editMode && (
					<input
						value={fullName}
						onChange={e => {
							SetFullName(e.currentTarget.value);
						}}
						autoFocus
						onBlur={() => {
							SetEditMode(false);
							props.updateFullName(fullName, props.serchPageInfo.aboutMe, props.serchPageInfo.jobDesc);
						}}
					/>
				)}
			</p>
		</div>
	);
};

export default EditFullNameForm;
