import React, { useEffect, useState } from 'react';
import cls from './style.module.scss';

let EditLookForJobDescForm = props => {
	let [editMode, SetEditMode] = useState(false);
	let [lookingForAJobDesc, SetLookingForAJobDesc] = useState(props.serchPageInfo.jobDesc);

	useEffect(
		() => {
			SetLookingForAJobDesc(props.serchPageInfo.jobDesc);
		},
		[props.serchPageInfo.jobDesc],
	);

	return (
		<div className={`line ${cls.line}`}>
			<p>Description : </p>
			<p
				onDoubleClick={() => {
					props.isOwner && SetEditMode(true);
				}}
			>
				{!editMode ? (props.serchPageInfo.jobDesc ? props.serchPageInfo.jobDesc : `no description`) : null}
				{editMode && (
					<textarea
						value={lookingForAJobDesc ? lookingForAJobDesc : null}
						onChange={e => {
							SetLookingForAJobDesc(e.currentTarget.value);
						}}
						autoFocus
						onBlur={() => {
							SetEditMode(false);
							props.updateLookForJobDesc(
								lookingForAJobDesc ? lookingForAJobDesc : 'noDesc',
								props.serchPageInfo.fullName,
								props.serchPageInfo.aboutMe,
								props.serchPageInfo.jobSearch,
							);
						}}
					/>
				)}
			</p>
		</div>
	);
};

export default EditLookForJobDescForm;
